<template>
    <base-card>
    <base-button @click="setSelectedTab('StoredResources')" :mode="storedResButtonMode">Stored Resources</base-button>
<base-button @click="setSelectedTab('AddResource')" :mode="addResButtonMode">Add Resource</base-button>
    </base-card>
    <keep-alive>
    <component :is="selectedTab" ></component>
</keep-alive>
</template>

<script>
import StoredResources from './StoredResources.vue';
import AddResource from './AddResource.vue';

export default {
    components:{
        StoredResources,
        AddResource,
    },
    data(){
        return{
            selectedTab: 'StoredResources',
            storedResources: [
                {
                    id: 'official-guide',
                    title: 'Official Guide',
                    description: 'The official vue documentation',
                    link: 'https://vuejs.org'
                },
                {
                    id: 'official-guide2',
                    title: 'Grepolis.com',
                    description: 'Bible',
                    link: 'https://Grepolis.com'
                }
            ],
        };
    },
    methods:{
        setSelectedTab(tab){
            this.selectedTab = tab;
        },
        addResource(title, desc, url){
            const newResource = {
                id : new Date().toString(),
                title: title,
                description: desc,
                link: url,
            } 
            this.storedResources.unshift(newResource);
            this.selectedTab = 'storedResources';
        },
        removeResource(id){
            const i = this.storedResources.findIndex( item => id === item.id );
            this.storedResources.splice(i, 1);
        }
    },
    provide(){
        return{
            resources: this.storedResources,
            addResource: this.addResource,
            removeResource: this.removeResource,
        }
    },
    computed:{
        storedResButtonMode(){
            return this.selectedTab === 'StoredResources' ? null : 'flat';
        },
        addResButtonMode(){
            return this.selectedTab === 'AddResource' ? null : 'flat';
        }
    }
}
</script>
