<template>
    <!-- dialog start --> 
    <base-dialog v-if="isInvalid" @close="confirmError" title="Invalid Title">

    <template #default>
        <p>One of the input is invalid</p>
        <p>Check them</p>
    </template>

    <template #actions>
    <base-button @click="confirmError">Okay</base-button>
    </template>

    </base-dialog>
    <!-- dialog ends --> 

    <!-- base card starts --> 
    <base-card>
    <form type="">
        <div class="form-control">
            <label for="title">Title</label>
            <form type=""></form>
            <input type="text" name="title" id="title" ref="titleInput">
        </div>
        <div class="form-control">
            <label for="description">Description</label>
            <textarea type="text" name="description" id="description" row="3" ref="descInput"></textarea>
        </div>
    </form>
    <div class="form-control">
        <label for="link">Link</label>
        <input type="url" name="link" id="link" ref="linkInput" />
    </div>
    <base-button @click="submitData" type="submit">Submit</base-button>
    </base-card>
    <!-- base card ends --> 
</template>

<script>

export default {
    inject: ['addResource'],
    data(){
        return {
            isInvalid: false,
        }
    },
    methods:{
        submitData(){
            const eneteredTitle = this.$refs.titleInput.value;
            const eneteredDesc = this.$refs.descInput.value;
            const eneteredLink = this.$refs.linkInput.value;

            if(eneteredLink.trim() === '' || eneteredTitle.trim() === '' || eneteredDesc.trim() === ''){
                this.isInvalid = true;
                return;
            }
            this.addResource(eneteredTitle, eneteredDesc,eneteredLink);
        },
        confirmError(){
            this.isInvalid = false;
        },
    }

}

</script>



<style scoped>
label {
    font-weight: bold;
    display: block;
    margin-bottom: 0.5rem;
}

input,
textarea {
    display: block;
    width: 100%;
    font: inherit;
    padding: 0.15rem;
    border: 1px solid #ccc;
}

input:focus,
textarea:focus {
    outline: none;
    border-color: #3a0061;
    background-color: #f7ebff;
}

.form-control {
    margin: 1rem 0;
}
</style>
